$mobile: 360px;
$tablet: 768px;
$desktop: 1024px;
$medium-desktop: 1200px;

@mixin for-tablet {
  @media (min-width: $tablet) {
    @content;
  }
}

@mixin for-desktop {
  @media (min-width: $desktop) {
    @content;
  }
}

@mixin for-large-desktop {
  @media (min-width: $medium-desktop) {
    @content;
  }
}
