@use '../../style/variables' as *;
@use '../../style/mixins' as *;

.card {
  width: 100%;
  padding: 20px;
  background-color: $landing-white;
  border-radius: $card-radius;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;

  .info,
  .name {
    width: 100%;
    p.text {
      @include typography_text;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-align: center;
    }
  }
}
